.todos {
  display: flex;
  align-items: center;
  list-style: none;
  flex-direction: column;
  gap: 10px;

  &__item {
    background-color: greenyellow;
    width: 300px;
    border-radius: 10px;
    transition: transform 0.5s;
    border: 1px black solid;
    color: rgb(255, 68, 204);
    font-size: 20px;

    &:hover {
      transform: scale(1.3);
    }
  }
}
